h1{
    text-align: center;
    color :white;
}
.welcome-text{
    color: white;
    text-align: center;
    margin-top: 10px;
}
.home-container {
    background-image: url('https://xplorefuture.com/wp-content/uploads/2023/10/Dron_flag2_copy_1280x960.jpg');
     /* Update this path */
    background-size: contain;
    background-size: cover; /* Cover the entire screen */
    background-position: center; /* Center the image */
    height: 100vh; /* Full height of the viewport */
    max-width: 186vh;
    display: flex; /* Use flexbox to center content */
    flex-direction: column; /* Stack items vertically */
    justify-content: center; /* Center vertically */
    align-items: center; /* Center horizontally */
    color: white; /* Text color for contrast */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); /* Add text shadow for better readability */
}

.button-container {
    margin-top: 20px; /* Space between text and buttons */
}

.styled-button {
    background-color: blue; /* Light button background */
    color: white; /* Dark text color */
    border: none; /* No border */
    border-radius: 5px; /* Rounded corners */
    padding: 10px 20px; /* Padding around the text */
    margin: 10px; /* Space between buttons */
    cursor: pointer; /* Pointer on hover */
    transition: background-color 0.3s; /* Smooth background color transition */
}

.styled-button:hover {
    color: blue;
    background-color: rgba(255, 255, 255, 1); /* Change background on hover */
}
