/* Welcome.css */
.welcome-heading {
    font-size: 2rem; /* Increase font size for emphasis */
    color: white; /* Dark gray color for text */
    background-color: #f0f8ff; /* Light background color for contrast */
    padding: 10px 15px; /* Padding for spacing */
    border-radius: 8px; /* Rounded corners for a softer look */
    position: absolute; /* Position the heading */
    top: 20px; /* Distance from the top */
    left: 20px; /* Distance from the left */
    margin: 0; /* Remove default margin */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow effect */
    transition: transform 0.2s ease, box-shadow 0.2s ease; /* Smooth transitions */
}

/* Optional hover effect */
.welcome-heading:hover {
    transform: translateY(-2px); /* Slight upward movement on hover */
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2); /* Darker shadow on hover */
}

.welcome-container {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center items horizontally */
    height: 100vh; /* Full viewport height */
    background: url('https://skilloutlook.com/storage/2017/11/IIT-Patna.jpg') no-repeat center center/cover;
    color: white; /* Text color */
    padding: 20px; /* Add padding for spacing */
}

h1 {
    background: linear-gradient(135deg, #ff6a00, #ee0979);

 /* Gradient background */            
    color:blue;/* Sets the text color to white */
    padding: 18px;          /* Adds more padding for a spacious look */
    text-align: center;      /* Centers the text */
    border-radius: 10px;     /* Rounds the corners of the background */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /* Adds a subtle shadow for depth */
    font-family: 'Arial', sans-serif; /* Optional: changes font family */
    margin: 20px 0;         /* Optional: adds vertical margin */
}


.middle-container {
    display: flex;
    flex: 1; /* Take up the remaining space */
    justify-content: center; /* Center items vertically */
    align-items: center; /* Center items horizontally */
    width: 100%; /* Full width */
}

p {
    margin: 10px 0; /* Space between the <p> and the button */
}

button {
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: blue; /* Button color */
    border: none;
    border-radius: 5px;
    cursor: pointer;
    max-width: 200px; /* Optional: set a max width for the button */
    margin: 10px 10px;
    margin-top: 80px; /* Adjust this value to move the button down */
}


button:hover {
    background-color: darkblue; /* Button hover color */
}

.logout-button {
    background-color: red; /* Change background color as needed */
    color: white; /* Change text color */
    border: none;
    padding: 10px 20px;
    margin-left: 10px; /* Spacing between buttons */
    cursor: pointer;
    border-radius: 5px; /* Rounded corners */
}

.logout-button:hover {
    background-color: darkred; /* Change color on hover */
}
