.room-selection-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.rooms-grid {
    display: grid;
    grid-template-columns: repeat(15, 1fr); /* 5 rooms per row */
    gap: 20px;
}
.rooms-wrapper {
    background-color: #f0f8ff; /* Light blue color */
    padding: 20px; /* Add padding for spacing */
    border-radius: 8px; /* Rounded corners */
    margin: 20px 0; /* Margin for separation from other elements */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}
.room {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    color: white; /* Text color for room number */
    background-color: grey; /* Default color for room blocks */
    border-radius: 5px;
    cursor: pointer;
}

.available {
    background-color: green; /* Available rooms are green */
}

.booked {
    background-color: red; /* Booked rooms are red */
}

.room:hover {
    opacity: 0.8; /* Hover effect */
}

.show-details-button {
    background-color: #4CAF50; /* Green background */
    color: white; /* White text */
    padding: 10px 20px; /* Add some padding */
    border: none; /* Remove borders */
    border-radius: 5px; /* Rounded corners */
    font-size: 16px; /* Increase font size */
    cursor: pointer; /* Add a pointer cursor on hover */
    transition: background-color 0.3s ease; /* Smooth background color change */
    margin-top: 20px; /* Add some space above the button */
    display: block; /* Make the button take up the full width */
    width: 200px;
    text-align: center;
    margin-left: auto;
    margin-right: auto; /* Center align the button */
}

.show-details-button:hover {
    background-color: #45a049; /* Darker green on hover */
}

.hamburger-menu {
    position: absolute; /* Position relative to the nearest positioned ancestor */
    top: 20px; /* Distance from the top */
    left: 20px; /* Distance from the left */
    cursor: pointer; /* Change the cursor to pointer for better UX */
    z-index: 1000; /* Ensure it appears above other elements */
}


.hamburger-toggle {
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.hamburger-line {
    width: 30px;
    height: 4px;
    background-color: #000;
    border-radius: 2px;
}

.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    border: 1px solid #ccc;
    list-style-type: none;
    padding: 0;
    width: 150px;
}

.dropdown-menu li {
    padding: 10px;
    cursor: pointer;
}

.dropdown-menu li:hover {
    background-color: #f1f1f1;
}
.selectroom {
    font-size: 2rem; /* Increase font size */
    color: white; /* Dark gray color for the text */
    background-color: #f0f8ff; /* Light background color */
    padding: 20px; /* Add padding for breathing room */
    border-radius: 8px; /* Rounded corners */
    text-align: center; /* Center the text */
    margin: 20px 0; /* Margin above and below */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow effect */
    transition: transform 0.2s ease, box-shadow 0.2s ease; /* Smooth transitions */
}

.selectroom:hover {
    transform: translateY(-2px); /* Slight upward movement on hover */
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2); /* Darker shadow on hover */
}

.feedback-message {
    margin-top: 10px;
    padding: 10px;
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
    border-radius: 5px;
    display: inline-block;
}