/* Container for the success message */
.success-container {
    background-color: #f0f8ff; /* Light blue background */
    border-radius: 10px; /* Rounded corners */
    padding: 40px; /* Padding around the content */
    max-width: 600px; /* Limit the width of the container */
    margin: 50px auto; /* Center the container */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add a shadow for depth */
    text-align: center; /* Center-align text */
}

/* Heading styles */
.success-container h1 {
    color: white; /* Green color for success */
    font-size: 36px; /* Larger font size */
    margin-bottom: 20px; /* Space below the heading */
}

.success-container h2 {
    color: #333; /* Dark text color for the user's name */
    font-size: 28px; /* Font size for the name */
    margin-bottom: 10px; /* Space below the name */
}

/* Paragraph styles */
.success-container p {
    font-size: 18px; /* Larger font size for readability */
    color: #555; /* Grey text color */
    margin: 10px 0; /* Space between paragraphs */
}

/* Room booked info styling */
.success-container p:last-of-type {
    font-weight: bold; /* Bold the room information */
    color: #333; /* Darker color for room info */
}

/* Add some spacing for mobile responsiveness */
@media (max-width: 768px) {
    .success-container {
        padding: 20px;
    }
}

.back-button {
    background-color: #4caf50; /* Green color */
    color: white;
    border: none;
    padding: 12px 20px;
    margin-top: 20px;
    font-size: 1rem;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.back-button:hover {
    background-color: #45a049; /* Darker green on hover */
}

.back-button:active {
    background-color: #388e3c; /* Even darker green on click */
}

.back-button:focus {
    outline: none;
}
