/* Set the background image and style the page */
.register-container {
    background-image: url('https://xplorefuture.com/wp-content/uploads/2023/10/Dron_flag2_copy_1280x960.jpg'); /* Add the path to your college image */
    background-size: cover;
    background-position: center;
    min-height: 100vh; /* Full page height */
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Arial', sans-serif;
}

form {
    background-color: rgba(255, 255, 255, 0.8); /* White with slight transparency */
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    width: 350px;
    text-align: center;
}

h2 {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
}

input {
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
}

input:focus {
    border-color: #007bff;
    outline: none;
}

button {
    width: 100%;
    padding: 12px;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}

p {
    color: red;
    margin-top: 10px;
    font-size: 14px;
}

.login-link {
    color: blue;
    cursor: pointer;
    text-decoration: underline;
}

.login-link:hover {
    color: darkblue;
}